@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
html, body, #root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}
h1,h2,h3,p {
  margin: 0;
}
input, textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading, .error {
  text-align: center;
  margin: 40px auto;
}
.App {
  min-height: 100%;
}

.App.dark{
  background-color: #333;
}
.assignment-card .card.dark{
  background: rgb(117, 107, 107);
}

.assignment-card {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
  max-width: 1200px;
  margin: 40px auto;
}
.assignment-card .card {
  background: #fff;
  color: #333;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
  transition: all 0.3s ease;
  position: relative;
}
.assignment-card .card:hover {
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
}
.assignment-card .card h3 {
  color: #555;
  margin: 0;
  display: inline-block;
}
.assignment-card .card p {
  color: #999;
  font-size: 0.9em;
}
.assignment-card .card div {
  color: #555;
  font-size: 0.7em;
  margin: 20px 0;
  line-height: 1.5em;
}
.assignment-card .card a {
  color: #555;
  text-decoration: none;
  display: block;
  background: #e2e2e2;
  font-size: 0.9em;
  width: 100px;
  text-align: center;
  width: 120px;
  padding: 8px;
  border-radius: 4px;
  margin: 20px auto 0;
}

.assignment-card .card .delete-btn {
  float: right;
  margin-bottom: auto;
  cursor: pointer;
  color: #fff;
  background-color: #e2111b;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  border: 1px solid transparent;
  padding: 6px 12px;
  font-size: 16px;
  border-radius: 0.25rem;
  transition: color .15s ease-in-out;
}

.assignment-card .card .delete-btn:hover {
  color: #fff;
  background-color: #a33b41;
}




.home {
    max-width: 1200px;
    margin: 60px auto;
}
.create {
  color: #555;
  max-width: 480px;
  margin: 60px auto;
}
.create label span {
  display: block;
  margin: 30px 0 10px;
}

.create button {
  display: block;
  width: 100px;
  font-size: 1em;
  color: #fff;
  padding: 8px 20px;
  border: 0;
  border-radius: 4px;
  background-color: #58249c;
  cursor: pointer;
  text-decoration: none;
  margin: 20px auto;
}


.assignment.dark{
  background-color: rgb(110, 103, 103);
}
.assignment {
    max-width: 1200px;
    margin: 40px auto;
    text-align: center;
    background: #fff;
    padding: 40px;
    box-sizing: border-box;
  }

  .method {
    text-align: left;
    line-height: 1.5em;
  }
.navbar {
    background: #58249c;
    padding: 20px;
    color: #fff;
  }
  .navbar nav {
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
  }
  .navbar a.brand {
    margin-right: auto;
    color: #fff;
    text-decoration: none;
  }
  .navbar a:last-child {
    color: #fff;
    text-decoration: none;
    margin-left: 40px;
    padding: 8px;
    border: 1px solid #fff;
    border-radius: 4px;
  }
  .navbar a:last-child:hover {
    background: #fff;
    color: #333;
  }

  .mode-div {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-width: 1200px;
    margin: 20px auto;
  }

  .mode-div img {
    border: 2.5px rgb(131, 116, 116) dotted ;
    width: 24px;
    height: 24px;
    cursor: pointer;
  }
.searchbar form {
  display: flex;
  align-items: center;
}
.searchbar input {
  margin-left: 10px;
}

